
@import "../../global";
.Programs{
    display:flex;
    flex-direction: column;
    gap:2rem;
    padding:0 2rem;
}
.programs-header{
    display: flex;
    gap:5rem;
    font-weight: bold;
    font-size: 3rem;
    justify-content: center;
    color: white;
    text-transform: uppercase;
    font-style:italic;

}
.program-categories{
    display:flex ;   
    gap:1rem;

}
.category{
    display: flex;
    flex-direction: column;
    background-color: gray;
    padding:2rem;
    gap:1rem;
    color: white;
    justify-content: space-between;
    &:hover{
        background: $planCard;
        cursor: pointer;
    }
}
    .category>:nth-child(1){
width:2rem;
height: 2rem;
fill:white;

    }

.category>:nth-child(2){
font-size: 1rem;
font-weight: bold;
}
.category>:nth-child(3){
    font-size: 0.9rem;
    line-height: 25px;

}
.join-now{
    display: flex;
    align-items: center;
    gap:2rem;
    img{
        width:1rem;

    }
}


@media screen and (max-width:768px){
    .programs-header{
        flex-direction: column;
        gap: 1rem;
        font-size: xx-large;
        align-items: center;
        justify-content: center;
       

    }
    .program-categories{
        flex-direction: column;
        
    }
    
   
}

@media screen and (min-width:768px )  and(max-width:1170px){
    .programs-header{
        font-size: 2rem;
        text-align: center;
    }
    .category{
        padding: 1rem;
        :nth-child(1){
            width: fit-content;
        }
        :nth-child(3) {
            font-size: 0.7rem;
            line-height: 18px;
        }
    }
   
}